import { Component, OnInit } from '@angular/core';
import { DadesService } from '../services/dades.service'

@Component({
  selector: 'app-lonza',
  templateUrl: './lonza.page.html',
  styleUrls: ['./lonza.page.scss'],
})
export class LonzaPage implements OnInit {
  public vacunesL: Array<any>;
  public donesL: number = 0;
  public homesL: number = 0;
  public numtotalL: number = 0;
  public dosiHL: number = 0;
  public dosiDL = 0;

  constructor(private dades: DadesService) { }

  ngOnInit() {
    this.dades. dadesVacunes().subscribe(
      (data: Array<any>) => {
 //Recollir dades i guardarles a les propietats totals
 this.vacunesL= data;

 this.vacunesL.forEach(element => {  

   if(element.fabricant == "Moderna / Lonza"){
     console.log(element.fabricant)
     this.numtotalL = +this.numtotalL+ +element.recompte
     if(element.sexe == "Home"){
       console.log(element.fabricant)
       this.homesL = +this.homesL +1
       this.dosiHL = +this.dosiHL + +element.dosi
     }else{
       console.log(element.fabricant)
       this.donesL = +this.donesL +1
       this.dosiDL = +this.dosiDL + +element.dosi
     }
   }

 });
}
)
}
}


